import {Component} from "./../common/component";

import {FormatInt, FormatPercent} from "../common/fnFormat";

import * as d3Selection from 'd3-selection'
import * as d3Dispatch from 'd3-dispatch'
import * as d3Array from 'd3-array'
import * as d3Fetch from 'd3-fetch'
import * as d3Dsv from 'd3-dsv'
const d3=Object.assign({},d3Selection,d3Dispatch,d3Array,d3Fetch,d3Dsv);




class Results extends Component {

    static _type='_cResult';

    /**
     * CONSTRUCTEUR
     * @param {String}          id                   Identifiant
     */
    constructor() {
        super('Resultats');
        this._container = d3.create('div');
    }

    data(listeCircos,nuancesPol){
        this._circos = listeCircos;
        this._nuances= d3.group(nuancesPol.dataset,d=>d.code);
        return this;
    }

    update(circo){
        this.id=circo;
        this._metadata=this._circos.find('CIRCO_ID',circo);
        this._container.attr('id',`res${circo}`);
        this.empty();
        this._load();
        return this;
    }

    _title(){
        let title=[],
            circo = (this._metadata['CIRCO_NOM']===1) ? '1<sup>ère</sup>' : `${this._metadata['CIRCO_NOM']}<sup>e</sup>`;
        if (this._metadata['DEP_NOM']) title.push(this._metadata['DEP_NOM'].toUpperCase());
        title.push(circo+ ' circonscription');
        this._header = this._container.append('header');
        this._header.append('h3').text(this._metadata['REG_NOM']);
        this._header.append('h4').html(title.join(' / '));
        return this;
    }

    _load(){
        this.enqueue( () => new Promise((resolve) => {
            let fileName=`assets/data/circonscriptions/${this.id.replace('-','')}.csv`;
            try{
                d3.csv(fileName).then((dataset) => {
                        this._data=dataset.map(d3.autoType).sort((a,b)=> b.voix-a.voix );
                        resolve(this._data);
                    });
            }catch(error){
                console.warn(error);
            }

             /*   new DataCollection(`res${this.id}`)
                    .load(`assets/data/circonscriptions/${this.id.replace('-','')}.csv`)
                    .ready.then( (d)=>  {this._data=d.dataset.sort((a,b)=> b.voix-a.voix ); resolve(d.dataset);});*/

        }));
    }

    render(delay=0){
        delay = (!this._style.opacity)? delay : 0;
        setTimeout(()=>{
            this.enqueue( () => new Promise((resolve) => {
                this._title();
                let inscrits=this._data[0].inscrits,
                    votants=this._data[0].votants,
                    exprimes=this._data[0].exprimes,
                    participation=100 * votants/inscrits;
                //this._container.append('p').text(`Participation : ${FormatPercent(participation)}`);
                const   table = this._container.append('table'),
                        thead= table.append('thead'),
                        figs = new Map();
                figs.set('inscrits', { });
                figs.set('votants', { bold:true, className:'participation' });
                figs.set('blancs', { });
                figs.set('nuls', { });
                figs.set('exprimes', { label: 'Exprimés' });
                for (const [key, value] of figs){
                    let tr =  thead.append('tr'),
                        td0 = value.label || key.charAt(0).toUpperCase() + key.slice(1),
                        td1 = FormatInt (this._data[0][key]),
                        td2 = FormatPercent(100 * this._data[0][key]/inscrits );
                    tr.append('td').text(td0).style('padding-left','1.2rem');
                    tr.append('td').text(td1).classed('right',true);
                    tr.append('td').text(td2).classed('bold',value.bold).classed('right',true);
                    tr.append('td').text('').attr('colspan',2).classed(value.className,value.className);
                }
                this._container.select('td.participation').html('&nbsp;&nbsp;&nbsp;PARTICIPATION').classed('bold',true);
                table.append('tr').append('td').attr('colspan','5').classed('separator',true);
                table.append('tr').html('<th rowspan="2"></th><th rowspan="2" style="vertical-align: bottom;">voix</th><th colspan="2" style="text-align: center;">&nbsp;&nbsp;Pourcentage</th><th rowspan="2"></th>');
                table.append('tr').html('<th>inscrits</th><th>exprimés</th>');
                let ptd4;
                this._data.forEach( (d,i)=>{
                    let tr = table.append('tr'),
                        color = (this._nuances.has(d.nuance))? this._nuances.get(d.nuance)[0].couleur : '#ccc',
                        td0 = `<span style="background-color:${color};"></span>${d.nom} ${d.prenom} (${d.nuance})`,
                        td1 = FormatInt (d.voix),
                        td2 = FormatPercent(d.pct_inscrits),
                        td3 = FormatPercent(d.pct_exprimes),
                        td4 = (d.elu==='O')? 'élu(e)': (d.elu==='B')? 'ballotage' :'';
                    if (td4!=='') tr.classed('bold',true);
                    tr.append('td').html(td0).filter(()=> ((td4==='' && ptd4!=='') || i===0) ).style('padding-top','2vh');
                    tr.append('td').text(td1).classed('right',true);
                    tr.append('td').text(td2).classed('right',true);
                    tr.append('td').text(td3).classed('right',true);
                    tr.append('td').text(td4).classed('right',true);
                    ptd4=td4;
                });
                this.show();
                resolve(this);
            }))
        },delay);
    }

    show(){
        d3.select('div._Panel').style('display','none');
        this._parent.container.transition().duration(500).style('background','rgba(255,255,255,.9)');
        this.fadeIn();
        this._container.style('pointer-events','auto')
            .on('click',()=> this.hide() );
    }

    hide(){
        d3.select('div._Panel').style('display','block');
        this._parent.container.transition().duration(500).style('background','none');
        this.fadeOut();
        this.empty();
        this._container.style('pointer-events','none');
    }


}

export {Results}
